.menu-btn {
    outline: thin solid var(--first-color);
    border: 0;
    cursor: pointer;
    background-color: var(--second-color);
    display: flex;
    width: 2rem;
    height: 2rem;
    svg {
        fill: var(--first-color);
        align-self: center;
    }
}

.menu {
    position: fixed;
    left: 0;
    bottom: var(--header-height);
    //width: 100%;
    width: 100vw;
    display: flex;
    flex-direction: column;
    background-color: var(--second-color);
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.5s ease;
    &.is-active {
        opacity: 1;
        pointer-events: auto;
    }
    a {
        padding: 1rem;
        font-size: 1.5rem;
        font-weight: bold;
        text-align: center;
        text-decoration: none;
        color: var(--first-color);
        &:hover {
            color: var(--white-color);
            background-color: var(--first-color);
        }
    }
}

.active-spy {
    color: var(--white-color) !important;
}

@include tablet-landscape {
    .menu-btn {
        display: none;
    }
    .menu {
        position: static;
        width: auto;
        flex-direction: row;
        opacity: 1;
        pointer-events: auto;
        a {
            padding: 0 1rem;
            &:last-child {
                padding-right: 0;
            }
            &:hover {
                background-color: transparent;
            }
        }
    }
}