  .portfolio > .container {
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(2, 400px); }
    .portfolio > .container a:nth-of-type(1) {
      grid-column: span 2;
      grid-row: initial; }
    .portfolio > .container a:nth-of-type(2) {
      grid-column: span 2;
      grid-row: initial; }
    .portfolio > .container a:nth-of-type(3) {
      grid-column: span 2;
      grid-row: initial; }
    .portfolio > .container a:nth-of-type(4) {
      grid-column: span 3;
      grid-row: initial; }
    .portfolio > .container a:nth-of-type(5) {
      grid-column: span 3;
