.modal {
    position: fixed;
    z-index: 999;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--black-alpha-color);
    opacity: 0;
    pointer-events: none;
    transition: all 1s;
    .modal-content {
        position: relative;
        .modal-close {
            position: absolute;
            top: 1rem;
            right: 1rem;
            svg {
                width: 3rem;
                height: 3rem;
                fill: var(--first-color);
            }
        }
    }
    &[id|="trabajo"]:target {
        opacity: 1;
        pointer-events: auto;
    }
}