@import 'base/mixins';

html {
    //font-size: 62.5%;
    font-size: 16px;
    box-sizing: border-box;
    min-height: 100%;
    scroll-behavior: smooth;
    scroll-padding: 150px 0 0 50px;
}
*, *:before, *:after {
    box-sizing: inherit;
    outline: none;
}
body {
    font-family: $fuente_principal;
    //font-size: 1.6rem;
    min-height: 100%;
    overflow-x: hidden;
}

a {
    color: var(--link-color);
    transition: all 0.5s ease-out;
    &:hover {
        opacity: 0.75;
    }
  }
  
h1 {
    margin: 0;
    font-size: 2rem;
}

h2 {
    margin: 0;
    font-size: 1.5rem;
}

h3 {
    margin: 0;
    font-size: 1.25rem;
}

h4 {
    margin: 0;
    font-size: 1rem;
}

h5 {
    margin: 0;
    font-size: 0.85rem;
}

h6 {
    margin: 0;
    font-size: 0.7rem;
}

img {
    max-width: 100%;
    height: auto;
}

p {
    line-height: 1.6;
}