.contact-form {
    margin: 2rem auto;
    padding: 1rem;
    max-width: 800px;
    .input-wrapper {
        position: relative;
        padding-left: 0;
        .label-move {
            position: absolute;
            top: 0.5rem;
            left: 0;
            transition: .3s ease-in-out;
            pointer-events: none;
        }
    }
    >* {
        padding: 0.5rem;
        margin: 1rem auto;
        display: block;
        width: 100%;
    }
    input,
    textarea {
        font-size: 0.85rem;
        font-family: var(--font);
    }
    .input {
        border: 0;
        padding-left: 0;
        width: 100%;
        border-bottom: thin solid var(--gray-dark-color);
        padding-bottom: 0.3rem;
        &[type="submit"] {
            margin-top: 0;
            cursor: pointer;
            transition: all 0.5s ease-out;
            &:hover {
                opacity: 0.75;
            }
        }
        +.contact-form-error {
            color: var(--input-color-invalid);
            position: absolute;
            left: 0;
            bottom: -0.6rem;
            font-size: .8rem;
            &.is-active {
                display: block;
            }
        }
        &:not(:placeholder-shown) {
            //border-bottom: 1px solid var(--input-color-invalid);
            ~.label-move {
                color: var(--input-color-invalid);
                -webkit-transform: translate3d(0, -1rem, 0);
                transform: translate3d(0, -1rem, 0);
                font-size: .7rem;
            }
            &:invalid {
                background-image: url(../../../dist/img/icon_invalid.svg) !important;
                background-repeat: no-repeat;
                background-position: right 1.6rem center;
                border-color: var(--input-color-invalid) !important;
                outline: none;
                //background-color: rgba($color-red_eb5757, .1);
                box-shadow: none;
                ~.label-move {
                    color: var(--input-color-invalid)
                }
            }
            &:valid {
                background-image: url(../../../dist/img/icon_valid.svg) !important;
                background-repeat: no-repeat;
                background-position: right 1.6rem center;
                border-color: var(--input-color-valid);
                outline: none;
                box-shadow: none;
                ~.label-move {
                    color: var(--input-color-valid);
                }
            }
        }
        &:focus {
            border-bottom: 1px solid var(--gray-dark-color);
            ~.label-move {
                //color: var(--input-color-invalid);
                -webkit-transform: translate3d(0, -1rem, 0);
                transform: translate3d(0, -1rem, 0);
            }
        }
        &:required {
            &:valid {
                border-color: var(--input-color-valid);
                .label-move {
                    color: var(--input-color-valid);
                }
            }
            &:invalid {
                //border-color: var(--input-color-invalid);
                &:focus {
                    border-bottom: thin solid var(--gray-dark-color);
                    +.label-move {
                        color: var(--input-color-invalid);
                    }
                }
            }
        }
    }
    textarea {
        border: thin solid var(--gray-dark-color);
        resize: none;
    }
    *::placeholder {
        color: var(--gray-dark-color);
    }
}

.contact-form-response {
    padding: 1rem;
    width: 400px;
    text-align: center;
    background-color: var(--white-color);
    svg {
        margin-top: 2rem;
        width: 4rem;
        height: 4rem;
        fill: var(--first-color);
    }
}

@media screen and (min-width: 1024px) {
    .contact-form {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        column-gap: 1rem;
        input,
        textarea {
            font-size: 1rem;
        }
        textarea,
        .contact-form-loader {
            grid-column: span 2;
        }
        input {
            &[type="submit"] {
                margin-left: 0;
                padding: 1rem;
            }
        }
    }
}