.hero-image {
    background-image: url(url(../../dist/img/hero-image-home.png));
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
}

.testimonials {
    .hero-image {
        background-image: url(../../dist/img/hero-image-customers.jpg);
    }
}

.hero-image-opacity {
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-color: var(--hero-opacity-color);
}

.hero-image-title {
    font-size: 7.5vw;
    color: var(--hero-text-color);
    margin-bottom: 3rem;
}