sans-serif;
// Tamaño de Media Queries
$telefono: 360px;
$tablet: 768px;
$tablet-landscape: 1024px;
$desktop: 1230px;
//separación
$separacion: .8rem;
// Fuentes
$regular-400: 400;
$bold-700: 700;
$bold-900: 900;
:root {
    --first-color: #d90062;
    --first-alpha-color: rgba(217, 0, 98, 0.75);
    --second-color: #14192d;
    --second-alpha-color: rgba(20, 25, 45, 0.75);
    --third-color: #501464;
    --third-alpha-color: rgba(80, 20, 100, 0.75);
    --white-color: #fff;
    --gray-light-color: #f3f3f3;
    --gray-color: #ccc;
    --gray-dark-color: #666;
    --black-color: #000;
    --link-color: #509ee3;
    --title-color: #333;
    --text-color: #222;
    --input-color-valid: rgb(8, 123, 255);
    --input-color-invalid: rgb(206, 21, 21);
    --white-alpha-color: rgba(255, 255, 255, 0.5);
    --black-alpha-color: rgba(0, 0, 0, 0.5);
    //--font: "Raleway", sans-serif;
    --max-width: 1200px;
    --header-height: 4rem;
}